.hidden {
  display: none;
}

.highlight {
  background-color: #eee;
  margin: 0 0 30px;
  padding: .2em 1.5em;
  font-size: 1.2em;
}

.warning {
  color: red;
  font-weight: 400;
}

@media screen and (min-width: 1000px) {
  div#links a {
    line-height: .8em;
  }
}

audio {
  max-width: 100%;
}

body {
  word-break: break-word;
  margin: 0;
  padding: 1em;
  font-family: Roboto, sans-serif;
  font-weight: 300;
}

button {
  color: #fff;
  background-color: #225ece;
  border: none;
  border-radius: 2px;
  margin: 0 0 1em;
  padding: .5em .7em .6em;
  font-family: Roboto, sans-serif;
  font-size: .8em;
}

button:active {
  background-color: #225ece;
}

button:hover {
  background-color: #4372ca;
}

button[disabled] {
  color: #ccc;
}

button[disabled]:hover {
  background-color: #d84a38;
}

canvas {
  max-width: 100%;
  width: 100%;
  background-color: #ccc;
}

code {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

div#container {
  max-width: 60em;
  margin: 0 auto;
  padding: 1em 1.5em 1.3em;
}

div#links {
  padding: .5em 0 0;
}

h1 {
  border-bottom: 1px solid #ccc;
  margin: 0 0 .8em;
  padding: 0 0 .2em;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

h2 {
  color: #444;
  font-weight: 500;
}

h3 {
  color: #666;
  white-space: nowrap;
  border-top: 1px solid #eee;
  margin: 10px 0;
  font-weight: 500;
}

li {
  margin: 0 0 .4em;
}

html {
  overflow-y: scroll;
}

img {
  max-width: 100%;
  border: none;
}

input[type="radio"] {
  position: relative;
  top: -1px;
}

p {
  color: #444;
  font-weight: 300;
}

p#data {
  max-height: 1000px;
  border-top: 1px dotted #666;
  padding: 1em 0 0;
  font-family: Courier New, monospace;
  line-height: 1.3em;
  overflow-y: auto;
}

p.borderBelow {
  border-bottom: 1px solid #aaa;
  padding: 0 0 20px;
}

section p:last-of-type {
  margin: 0;
}

section {
  border-bottom: 1px solid #eee;
  margin: 0 0 30px;
  padding: 0 0 20px;
}

section:last-of-type {
  border-bottom: none;
  padding: 0 0 1em;
}

select {
  margin: 0 1em 1em 0;
  position: relative;
  top: -1px;
}

h1 span {
  white-space: nowrap;
}

a {
  color: #1d6eee;
  font-weight: 300;
  text-decoration: none;
}

h1 a {
  white-space: nowrap;
  margin: 0 10px 0 0;
  font-weight: 300;
}

a:hover {
  color: #3d85c6;
  text-decoration: underline;
}

a#viewSource {
  border-top: 1px solid #999;
  margin: 1.3em 0 0;
  padding: 1em 0 0;
  display: block;
}

div#errorMsg p {
  color: red;
}

div#links a {
  margin: 0 0 1.5em;
  line-height: 1.3em;
  display: block;
}

div.outputSelector {
  margin: -1.3em 0 2em;
}

p.description {
  margin: 0 0 .5em;
}

strong {
  font-weight: 500;
}

textarea {
  resize: none;
  font-family: Roboto, sans-serif;
}

video {
  --width: 100%;
  width: var(--width);
  height: calc(var(--width) * .75);
  background: #222;
  margin: 0 0 20px;
}

ul {
  margin: 0 0 .5em;
}

@media screen and (max-width: 650px) {
  .highlight {
    margin: 0 0 20px;
    padding: .2em 1em;
    font-size: 1em;
  }

  h1 {
    font-size: 24px;
  }
}

@media screen and (max-width: 550px) {
  button:active {
    background-color: #8b0000;
  }

  h1 {
    font-size: 22px;
  }
}

@media screen and (max-width: 450px) {
  h1 {
    font-size: 20px;
  }
}

